.btn {
  position: relative;
  z-index: 0;
  overflow: hidden;
}

.btn {
  position: relative;
  display: inline-block;
  border: 1px solid #ffffff;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  transition: 1s;
}

.btn:hover {
  color: #111111;
  transition: all 0.5s;
}

.btn.btn-group:hover {
  color: #111111;
  transition: all 0.5s;
}
@media screen and (min-width: 768px) {
  .btn.btn-group:hover {
    color: #111111;
    transition: all 0.5s;
  }
}

.btn::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  content: "";
  background: #ffffff;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform 1s;
}

.btn.btn-group::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  content: "";
  background: #ffffff;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform 1s;
}

.btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

